<template>
    <div class="whole-plan-generate">
        <div class="topbar">
            <p>路线信息</p>
            <div class="path-info">
                <ul>
                    <li v-for="(item,key) of data.routeList" :key="key">
                        <span class="path">{{item.departurePort}}</span>
                        <svg class="icon icon-train" aria-hidden="true">
                            <use v-if="item.routeList.trspType === '铁运'" fill="#FFBB32" xlink:href="#icon-andaotongcaidanICON_huoche"></use>
                            <use v-if="item.routeList.trspType === '公路'" fill="#FFBB32" xlink:href="#icon-huoche"></use>
                            <use v-if="item.routeList.trspType === '水运'" fill="#FFBB32" xlink:href="#icon-andaotongcaidanICON_lunchuan"></use>
                            <use v-if="item.routeList.trspType === '空运'" fill="#FFBB32" xlink:href="#icon-feiji800"></use>
                        </svg>
                        <small>{{item.routeList.logisticsType}}</small>
                        <svg class="iconfont icon-arrows" aria-hidden="true">
                            <use xlink:href="#icon-andaotongcaidanICON_jiantou"></use>
                        </svg>
                        <template v-if="key===data.routeList.length-1">
                            <span class="path last-path">{{item.destinationPort}}</span>
                        </template>
                    </li>
                </ul>
                <p class="desc">
                    <span>物流委托编号：{{data.entrustCode}}</span>
                    <span>物流计划时间：2020/06/22  15:20  --  2020/06/22  15:20</span>
                    <span>时长：2020小时</span>
                    <span>费用：{{data.totalPrices}}{{data.curr}}</span>
                </p>
            </div>
        </div>
        <div class="content">
            <div class="top-title">
                <p class="tit">物流全程计划</p>
                <p class="btn">
                    <Button class="left">上报客户/销售</Button>
                    <Button type="primary" @click="save('formValidate')">保存计划</Button>
                </p>
            </div>
            <div class="box">
                <div class="plan">
                    <ul>
                        <li v-for="(item, key) of nodeList" :key="key">
                            <span class="radio"></span>
                            <span class="line-shu">
                                <svg class="iconfont" aria-hidden="true">
                                    <use xlink:href="#icon-andaotongcaidanICON_xiaojiantou"></use>
                                </svg>
                            </span>
                            <span class="port">{{item.port}}</span>
                            <p class="line-class">
                                <span class="line-top">----</span>
                                <span class="line">----------</span>
                                <span class="line2">----</span>
                                <span class="line3">-----------------</span>
                            </p>
                            <svg class="iconfont" aria-hidden="true">
                                <use fill="#02ADB5" xlink:href="#icon-andaotongcaidanICON_xiaojiantou"></use>
                            </svg>
                            <p class="btn">
                                <span class="button" @click="btnClick(key)" :class="{'button-click': clickBtnNum == key}">节点服务设置</span>
                                <svg class="iconfont" aria-hidden="true" v-show="clickBtnNum == key">
                                    <use fill="#02ADB5" xlink:href="#icon-andaotongcaidanICON_xiaojiantou"></use>
                                </svg>
                            </p>
                            <p class="btn btn-two">
                                <span class="button" @click="btnTwoClick(key)" :class="{'button-click': clickBtnTwoNum == key}">运输服务设置</span>
                                <svg class="iconfont" aria-hidden="true" v-show="clickBtnTwoNum == key">
                                    <use fill="#02ADB5" xlink:href="#icon-andaotongcaidanICON_xiaojiantou"></use>
                                </svg>
                            </p>
                        </li>
                    </ul>
                </div>
                <div class="plan-content">
                    <adt-tab-btns :height="40" :width="150" :isUnderline="isUnderline" :titleNames="titleNames" :btnIndex="changeIndex" @handleBtn="handleBtn"/>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            data: {
                "entrustCode": "LC202007271425024631",
                "logisticsSchemeCode": "LP202007201924010001",
                "schemeValidity": "5",
                "schemeValidityUnit": "D",
                "totalPrices": 2200,
                "curr": "CNY",
                routeList: [
                    {
                        "logisticsSchemeCode": "LP202007201924010001",
                        "departurePort": "20201001",
                        "destinationPort": "20201002",
                        "routeCode": "RT1001",
                        "roadCode": "R001",
                        "trspMode": "3",
                        "logisticsType": "N",
                        "trspType": "C",
                        "routeList": [
                            {
                                "serverCode": "PT202007282130480926",
                                "serverName": "运输服务",
                                "serverType": "T"
                            }
                        ]
                    },
                    {
                        "logisticsSchemeCode": "LP202007201924010001",
                        "departurePort": "20201002",
                        "destinationPort": "20201010",
                        "routeCode": "RT1001",
                        "roadCode": "R002",
                        "trspMode": "4",
                        "logisticsType": "N",
                        "trspType": "C",
                        "routeList": [
                            {
                                "serverCode": "PT202007282130480926",
                                "serverName": "运输服务",
                                "serverType": "T"
                            }
                        ]
                    }
                ],
                roadList: [
                    {
                        "nodeCode": "20201002",
                        "nodeName": null,
                        "nodeType": "N",
                        "roadList": [
                            {
                                "serverCode": "LP202007201924010001",
                                "serverName": "LP202007201924010001",
                                "serverType": "Y"
                            },
                            {
                                "serverCode": "PS202007271517098538",
                                "serverName": "PS202007271517098538",
                                "serverType": "Y"
                            }
                        ]
                    }
                ]
            },
            clickBtnNum: 0,
            clickBtnTwoNum: -1,
            titleNames: ['关务服务', '场所服务', '综保区服务'],
            changeIndex: 1,
            isUnderline: true,
            nodeList: []
        }
    },
    mounted() {
        let portVal = []
        for (let index = 0; index < this.data.routeList.length; index++) {
            const item = this.data.routeList[index];
            //for (const item of this.data.routeList) {
            let affix = {}
            this.$set(affix, 'departurePort', item.departurePort)
            this.$set(affix, 'destinationPort', item.destinationPort)
            this.$set(affix, 'port', item.departurePort)
            this.$set(affix, 'routeList', [])
            for (const list of item.routeList) {
                let routeListVal = {}
                this.$set(routeListVal, 'serverCode',list.serverCode)
                this.$set(routeListVal, 'serverType',list.serverType)
                this.$set(routeListVal, 'serverName',list.serverName)
                affix.routeList.push(routeListVal)
            }
            portVal.push(affix)
            if(index==this.data.routeList.length-1){
                affix = {}
                this.$set(affix, 'departurePort', item.departurePort)
            this.$set(affix, 'destinationPort', item.destinationPort)
            this.$set(affix, 'port', item.destinationPort)
            this.$set(affix, 'routeList', [])
             portVal.push(affix)
            }
        }
        portVal.forEach(element => {
            this.data.roadList.forEach(road => {
                if(element.port === road.nodeCode){
                    this.$set(element,"roadList", road.roadList)
                }
            })
        });
        this.nodeList = portVal
        console.log(this.nodeList)
    },
    methods: {
        btnClick(key) {
            this.clickBtnNum = key
            this.clickBtnTwoNum = -1
            console.log('节点服务'+key)
        },
        btnTwoClick(key) {
            this.clickBtnTwoNum = key
            this.clickBtnNum = -1
            console.log('运输服务'+key)
        },
        handleBtn (index, titleItem) {
            this.changeIndex = index + 1
            // if (titleItem == '设置运输要求') {
            //     this.tabBarShow = false
            // } else {
            //     this.tabBarShow = true
            // }
        },
    }
}
</script>
<style lang="scss" scoped>
.whole-plan-generate{
    .topbar{
        box-shadow:0px 2px 4px 0px rgba(51,51,51,0.1);
        .path-info{
            padding: 20px;
            background: #ffffff;
            ul{
                background: #f9f9f9;
                padding: 25px 42px;
                white-space: nowrap;
                overflow-x: auto;
                li{
                    display: inline-block;
                    vertical-align: middle;
                    .path{
                        vertical-align: middle;
                    }
                    .icon-train{
                        width: 22px;
                        height: 20px;
                        color: $warning-color;
                        vertical-align: middle;
                    }
                    small{
                        color: $light-color;
                    }
                    .icon-arrows{
                        width: 76px;
                        color: $light-color;
                    }
                    &:first-child{
                        .path{
                            font-size: 20px;
                            font-weight: bold;
                        }
                    }
                    &:last-child{
                        .last-path{
                            font-size: 20px;
                            font-weight: bold;
                        }
                    }
                }
            }
            .desc{
                margin-top: 20px;
                span{
                    display: inline-block;
                    margin-right: 100px;
                    &:last-child{
                        margin-right: 0;
                    }
                }
            }
        }
    }
    .content{
        .top-title{
            margin-top: 10px;
            padding: 10px 42px;
            .tit{
                display: inline-block;
                color: #333;
            }
            .btn{
                display: inline-block;
                position: absolute;
                right: 20px;
                .left{
                    margin-right: 20px;
                }
            }
        }
        .box{
            margin-top: 10px;
            .plan{
                width: 43%;
                display: inline-block;
                padding: 20px 32px;
                padding-right: 10px;
                background: #fff;
                min-height: 600px;
                ul{
                    overflow: visible;
                }
                li{
                    position: relative;
                    overflow: visible;
                    margin-top: 80px;
                    &:first-child{
                        margin-top: 0;
                        .line-top{
                            // display: none;
                        }
                    }
                    // &:last-child{
                    //     .line-shu{
                    //         display: none;
                    //     }
                    //     .line2{
                    //         display: none;
                    //     }
                    //     .line3{
                    //         display: none;
                    //     }
                    //     .btn-two{
                    //         display: none;
                    //     }
                    // }
                    .radio{
                        display: inline-block;
                        width: 10px;
                        height: 10px;
                        border-radius: 50%;
                        background: #E5E5E5;
                        margin-right: 15px;
                    }
                    .line-shu{
                        position: absolute;
                        width: 1px;
                        top: 36px;
                        left: 4px;
                        height: 66px;
                        background: #E5E5E5;
                        .iconfont{
                            position: absolute;
                            bottom: -16px;
                            left: -4px;
                            transform: rotate(90deg);
                            color: #E5E5E5;
                        }
                    }
                    .port{
                        display: inline-block;
                        width: 240px;
                        background: #FFF6E5;
                        font-size: 20px;
                        padding: 4px 10px;
                    }
                    .iconfont{
                        font-size: 10px;
                        vertical-align: middle;
                    }
                    .line-class{
                        display: inline-block;
                        position: relative;
                        .line-top{
                            color: #999;
                            position: absolute;
                            left: 0;
                            top: -10px;
                            color: #999;
                        }
                        .line{
                            color: #999;
                        }
                        .line2{
                            position: absolute;
                            left: 0;
                            bottom: -10px;
                            color: #999;
                        }
                        .line3{
                            position: absolute;
                            top: 22px;
                            left: 12px;
                            writing-mode: tb-rl;
                            height: 98px;
                            color: #999;
                        }
                    }
                    .btn{
                        display: inline-block;
                        margin-left: 14px;
                        .button{
                            display: inline-block;
                            font-size: 12px;
                            height: 32px;
                            padding: 10px 18px;
                            line-height: 1;
                            color: #515a6e;
                            border: 1px solid rgba(153,153,153,1);
                            border-radius: 4px;
                            cursor: pointer;
                        }
                        .button-click{
                            background: $light-color;
                            color: #fff;
                            border: 1px solid $light-color;
                        }
                    }
                    .btn-two{
                        position: absolute;
                        top: 166%;
                        right: 0;
                        background: #fff;
                        width: 120px;
                    }
                }
            }
            .plan-content{
                display: inline-block;
                vertical-align: top;
                width: 54%;
                background: #fff;
                margin-left: 20px;
                padding: 20px;
            }
        }
    }
}
</style>